<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h4>A few more details...</h4>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="onAddUserType">
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="name"
                label="Name"
                id="name"
                v-model="name"
                required></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <div>What type of user are you?</div>
              <v-select
               v-bind:items="itemsuserType"
                name="userType"
                label="userType"
                v-model="userType"
                required></v-select>
            </v-flex>
          </v-layout>
		  <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn
                class="primary"
                :disabled="!formIsValid"
                type="submit">Let's get online!</v-btn>
            </v-flex>
          </v-layout>
		  </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      name: '',
      userType: '',
      itemsuserType: [
        { text: 'Vet' },
        { text: 'Farmer' }
      ]
    }
  },
  computed: {
    formIsValid () {
      return this.name !== '' && this.userType !== ''
    }
  },
  methods: {
    onAddUserType () {
      if (!this.formIsValid) {
        return
      }
      const userData = {
        name: this.name,
        userType: this.userType
      }
      this.$store.dispatch('adduserType', userData)
      if (userData.userType.text === 'Farmer') {
        this.$router.push('/home')
      } else {
        this.$router.push('/vet')
      }
    }
  }
}
</script>



